<template>
  <!-- 注册页面的整体盒子 -->
  <div class="reg-container">
    <!-- 注册的盒子 -->
    <div class="reg-box">
      <!-- 标题的盒子 -->
      <div class="title-box"></div>
      <!-- 注册的表单区域 -->
    <el-form
    :status-icon='true'
    :model="regFrom"
    :rules="regRules"
    ref="regFrom"
    class="reg-form">
    <el-form-item prop="username">
    <el-input placeholder="请输入用户名"
     v-model="regFrom.username"></el-input>
    </el-form-item>
     <el-form-item prop="password">
    <el-input placeholder="请输入密码"
     v-model="regFrom.password"></el-input>
    </el-form-item>
     <el-form-item prop="repassword">
    <el-input placeholder="请再次输入密码"
     v-model="regFrom.repassword"></el-input>
    </el-form-item>
    <el-form-item>
        <el-button @click="regHandler" class='reg-btn' type="primary">注册</el-button><br>
        <el-link @click="$router.push('/login')" type="info">去登录</el-link>
    </el-form-item>
    </el-form>
    </div>
  </div>
</template>
<script>
export default {
  name: 'Reg',
  data () {
    //   validator
    // rule: 规则对象 value: 当前校验规则对应的数据项 callback: 回调函数不传参表示通过，传入错误对象表示不通过
    const checkPassword = (rule, value, callback) => {
      console.log(this)
      // 两次密码比对
      if (value === this.regFrom.password) {
        callback()
      } else {
        callback(new Error('两次输入不一致'))
      }
    }
    return {
      regFrom: {
        username: '',
        password: '',
        repassword: ''
      },
      regRules: {
        //   每个表单项的规则是一个数组
        // 一个表单有过很多条规则来校验
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          //   pattern 正则
          { pattern: /^[a-zA-Z][0-9a-zA-Z]{0,9}$/, message: '请输入1-10位的数字或数字（必须以字母开头）', trigger: 'blur' }
        ],

        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { pattern: /^\S{6,15}$/, message: '请输入6-15位的非空字符串', trigger: 'blur' }
        ],
        repassword: [
          { required: true, message: '请输入确认密码', trigger: 'blur' },
          { validator: checkPassword, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    regHandler () {
      // async 加在 await 所在的函数声明处，两个必须成对出现
      this.$refs.regFrom.validate(async result => {
        console.log(result)
        if (!result) return
        // 发送ajax请求进行注册 参数1:url 参数2:要传入的数据对象 {username:'xxx}
        await this.$http.post('/api/reg', this.regFrom)
        // console.log(res)
        // if (res.code !== 0) return this.$message.error(res.message)
        // this.$message.success(res.message)
        // 利用$router路由导航对象进行跳转
        // 可以直接传入字符串表示路径
        this.$router.push('/login')
      })
    }
  }
}
</script>

<style lang="less" scoped>
.reg-container {
  background: url('../.././assets/images/login_bg.jpg') center;
  background-size: cover;
  height: 100%;

  .reg-box {
    width: 400px;
    height: 335px;
    background-color: #fff;
    border-radius: 3px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);

    .title-box {
      height: 60px;
      background: url('../../assets/images/login_title.png') center no-repeat;
    }
    .reg-form{
        padding: 0 30px;
    }
    .reg-btn{
        width: 100%;
    }
  }
}
</style>
